@import url('https://fonts.googleapis.com/css?family=Raleway:400,700');

*,*:before,*:after{box-sizing:border-box}

body{
  min-height:100vh;
  font-family: 'Raleway', sans-serif;
}

.container{
  position:absolute;
  width:100%;
  height:100%;
  overflow:hidden;
  
  &:hover,&:active{
    .top, .bottom{
      &:before, &:after{
        margin-left: 200px;
        transform-origin: -200px 50%;
        transition-delay:0s;
      }
    }
    
    .center{
      opacity:1;
      transition-delay:0.2s;
    }
  }
}

.top, .bottom{
  &:before, &:after{
    content:'';
    display:block;
    position:absolute;
    width:200vmax;
    height:200vmax;
    top:50%;left:50%;
    margin-top:-100vmax;
    transform-origin: 0 50%;
    transition:all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
    z-index:10;
    opacity:0.65;
    transition-delay:0.2s;
  }
}

.top{
  &:before{transform:rotate(45deg);background:#4D6496;}
  &:after{transform:rotate(135deg);background:#B9BBEE;}
}

.bottom{
  &:before{transform:rotate(-45deg);background: #3C3FA0;}
  &:after{transform:rotate(-135deg);background:rgb(198, 199, 226);}
}

.center{
  position:absolute;
  width:400px;
  height:400px;
  top:53%;left:50%;
  margin-left:-200px;
  margin-top:-200px;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding:30px;
  opacity:0;
  transition:all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
  transition-delay:0s;
  color:#333;

  
  input{
    width:100%;
    padding:15px;
    margin:5px;
    border-radius:1px;
    border:1px solid #ccc;
    font-family:inherit;
  }
}

#get-password {
  font-size: 0.8rem;
}

#sign-in-button {
  height: 6vh;
  border-radius: 6px;
  border: none;
  width: 12vw;
  background-color: rgb(162, 177, 209);
  color: rgb(31, 30, 30);
  font-weight: 700;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 1px;
  font-size: 0.9rem;
}

#error-txt {
  font-size: 0.8rem;
}

